<script>
import parseJson from "@/utils/parseJson";
export default {
    name: "page-supplier-info",
    data() {
        return {
            loading: false,
            natureList: ["", "", "", "", ""],
            workTypeList: ["", "", ""],
            info: {}
        };
    },
    created() {
        this.getInfo();
    },
    activated() {
        this.getInfo();
    },
    methods: {
        getInfo() {
            const id = this.$route.params.id;
            this.$http
                .get(`/haolifa/supplier/info?id=${id}`)
                .then(res => {
                    res = parseJson(res, [
                        "credentialsInfo",
                        "financialInfo",
                        "mainOrgan",
                        "qualityAssuranceInfo",
                        "staffInfo"
                    ]);
                    if (res.mainOrgan && res.mainOrgan.length)
                        res.mainOrgan.forEach(item => {
                            item.id = Math.random();
                        });
                    if (
                        res.financialInfo &&
                        res.financialInfo.businessStateList &&
                        res.financialInfo.businessStateList.length
                    ) {
                        res.financialInfo.businessStateList.forEach(item => {
                            item.id = Math.random().toString();
                        });
                    }
                    this.info = res;
                })
                .catch(e => {
                    this.$toast(e.msg);
                });
        }
    }
};
</script>

